.App {
  color: #333;
}

@font-face {
  font-family: "chessline";
  src: url("CHEQ_TT.TTF");
  /* TTF file for CSS3 browsers */
}

@font-face {
  font-family: "ultraminimal";
  src: url("ultraminimal.ttf");
  /* TTF file for CSS3 browsers */
}

.container {
  width: 780px !important;
}

.label {
  font-size: 11px;
}

.footer {
  text-align: center;
  position: fixed;
  bottom: 0px;
  width: 100%;
}

.timer {
  /* margin: 60px 0px; */
  font-family: monospace;
  /* padding: 0px 50px; */
  font-size: 19px;
}

.chess-board {
  /* border:  7px solid #333; */
  /* opacity: 0.1; */
  cursor: default;
  /* padding: 6px 10px; */
  font-family: "chessline";
  text-align: center;
  font-size: 95px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.cell {}

.seperator {
  margin-top: -110px;
}

.selectable:hover {
  background: gold;
  cursor: pointer;
}

.selected, .selected:hover {
  background: gold !important;
}

.legalnext {
  background: gold !important;
  cursor: pointer;
}

#root > div > div:nth-child(1) > div:nth-child(1) > h1 {
  font-family: 'ultraminimal'
}

#lid {
  -webkit-animation: blink 5.4s infinite;
          animation: blink 5.4s infinite;
}

#eye {
  -webkit-animation: squeeze 5.4s infinite;
          animation: squeeze 5.4s infinite;
}

@-webkit-keyframes blink {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(15px) scaleY(0);
            transform: translateY(15px) scaleY(0)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}

@keyframes blink {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(15px) scaleY(0);
            transform: translateY(15px) scaleY(0)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}

@-webkit-keyframes squeeze {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(3px) scaleY(0.8);
            transform: translateY(3px) scaleY(0.8)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}

@keyframes squeeze {
  90% {
    -webkit-transform: none;
            transform: none;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(3px) scaleY(0.8);
            transform: translateY(3px) scaleY(0.8)
  }
  100% {
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
}

#thinking-bar {
  height: 1px;
  opacity: 0;
  -o-transition: opacity 0.3s;
  transition: opacity 0.3s;
  -webkit-transition: opacity 0.3s;
}


.ai-footprint-in-sand {
  background: white;
  -webkit-animation: normal-move 1s;  /* Safari 4.0 - 8.0 */
  -webkit-animation-iteration-count: 1; /* Safari 4.0 - 8.0 */
  animation: normal-move 1s;
  animation-iteration-count: 1;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes normal-move {
  0% {background: white;}
  10% {background: #C7C7C7;}
  100% {background: white;}
}

@keyframes normal-move {
  0% {background: white;}
  10% {background: #C7C7C7;}
  100% {background: white;}
}

.ai-bloody-footprint-in-sand {
  background: white;
  -webkit-animation: bloody-move 1.5s;  /* Safari 4.0 - 8.0 */
  animation: bloody-move 1.5s;
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes bloody-move {
  0% {background: white;}
  10% {background: #F83A3A;}
  100% {background: white;}
}

@keyframes bloody-move {
  0% {background: white;}
  10% {background: #F83A3A;}
  100% {background: white;}
}

.graveyard{
  font-family: "chessline";
  font-size: 36px;
  text-align: center;
}


